import React, { useContext, useState } from 'react';

import { YellowCreateNewWorkspace } from './styles';
import { history } from '../../services';
import { userContext, workspaceContext } from '../../state';
import { GreenPageContent, GreenPageTitle, GreenPageWrapper } from '../../ui/green-pages-styles';
import IconClosePage from '../../ui/icons/IconClosePage';
import { logout } from '../../utils/logout';
import { CancelContainer } from '../authentification/TwoFactor/components/styles';
import { createWorkspace, requestUserWorkspaces } from '../common/api';
import onWorkspaceCreated from '../common/on-workspace-created';
import switchWorkspace from '../common/switch-workspace';

const NoWorkspacePage: React.FC = () => {
  const [isProcessing, setIsProcessing] = useState<boolean>(false);

  const userCtx = useContext(userContext);
  const workspaceCtx = useContext(workspaceContext);

  const onCreateClick = async (): Promise<void> => {
    setIsProcessing(true);

    try {
      const availableWorkspaces = await requestUserWorkspaces();
      if (availableWorkspaces?.workspaces?.length) {
        const [workspace] = availableWorkspaces.workspaces;
        await switchWorkspace(workspace.id, workspaceCtx, userCtx);
        history.push('/');
      } else {
        const createdWorkspace = await createWorkspace(null, true);
        await onWorkspaceCreated(createdWorkspace.id, workspaceCtx, userCtx);
      }
    } finally {
      setIsProcessing(false);
    }
  };

  const onClose = (): Promise<void> => logout(workspaceCtx);

  return (
    <GreenPageWrapper>
      <GreenPageContent style={{ justifyContent: 'center' }}>
        <GreenPageTitle style={{ marginTop: '-97px', marginBottom: '52px', fontStyle: 'Roboto' }}>
          To continue using GoLogin,
          <br />
          create a workspace
        </GreenPageTitle>
        <YellowCreateNewWorkspace
          disabled={isProcessing}
          onClick={onCreateClick}
        >
          Create new workspace
        </YellowCreateNewWorkspace>
      </GreenPageContent>
      <CancelContainer onClick={onClose}>
        <IconClosePage />
      </CancelContainer>
    </GreenPageWrapper>
  );
};

export default NoWorkspacePage;
