import styled from '@emotion/styled';
import CloseIcon from '@material-ui/icons/Close';

export const GreenPageWrapper = styled.div`
  background: #00A987;
  display: flex;
  align-items: center;
  flex-direction: column;
  min-width: 100vw;
  padding-bottom: 30px;
  flex: 1;
`;

export const GreenPageContent = styled.div`
  width: 540px;
  height: 567px;
  color: #ffffff;
  display: flex;
  flex-direction: column;
  align-items: center;
`;

export const GreenPageTitle = styled.div`
  font: 700 38px/45.6px 'Graphik LC', Roboto;
  text-align: center;
  margin-bottom: 40px;
`;

export const GreenPageTitleTwoLines = styled(GreenPageTitle)`
  width: 448px;
  height: 92px;
`;

export const GreenPageSubTitle = styled('div')`
  font: 700 18px/18px 'Graphik LC', Roboto;
  margin-left: 17px;
  margin-right: 17px;
  min-width: 52px;
`;

export const GreenPageText = styled('div')`
  font: 400 24px/24px 'Graphik LC', Roboto;
`;

export const GreenPageYellowButton = styled.button`
  outline: none;
  border: none;

  cursor: pointer;

  font: 400 18px/21.6px 'Graphik LC', Roboto;
  color: #000000;
  background: #FFE164;
  border-radius: 60px;
  padding: 17px 30px;
  margin-top: 32px;
`;

export const GreenPageYellowButtonThin = styled(GreenPageYellowButton)`
  font-weight: 300;
`;

export const  GreenPageTextarea = styled('textarea')`
  font: 400 18px/21px 'Calibri', Roboto;

  margin-top: 16px;
  resize: none;
  border-radius: 4px;
  box-sizing: border-box;
  width: 100%;
  height: 257px;

  padding: 15px 17px;

  color: #000000;
  background: #ffffff;
  border: none;

  :focus {
    outline: none !important;
  }

  &::placeholder {
    color: #777;
  }
`;

export const GreenPageInput = styled('input')`
  font: 400 16px/21px Roboto;

  margin-top: 16px;
  resize: none;
  border-radius: 4px;
  box-sizing: border-box;
  width: 404px;

  padding: 15px 17px;

  color: #000000;
  background: #ffffff;
  border: none;

  :focus {
    outline: none !important;
  }

  &::placeholder {
    color: #777;
  }
`;

export const ClosePageIcon = styled(CloseIcon)`
  position: absolute;
  top: 32px;
  right: 32px;
  cursor: pointer;
  color: #ffffff;
  opacity: 0.5;

  &:hover {
    opacity: 0.7;
  }
`;
